html,
body,
div {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
.ui.inline.loader {
  top: 50%;
}
.ui.segment {
  margin: 0;
  border-radius: 0;
}
#menu {
  background-color: #F5F5F5;
  width: 260px;
}
#menu div {
  height: auto;
  width: auto;
  min-height: 0;
}
#appbar {
  position: absolute;
  height: 56px;
  background-color: #009688;
  z-index: 1;
}
#sidebar-icon {
  position: absolute;
  top: 18px;
  left: 18px;
  color: #FFFFFF;
}
#options-icon {
  position: absolute;
  top: 18px;
  right: 18px;
  color: #FFFFFF;
}
div #create-newfile {
  position: absolute;
  height: 56px;
  width: 100%;
  bottom: 0;
  left: 0;
}
#create-newfile i {
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
  color: #969696;
}
#contents {
  position: absolute;
  top: 0;
  bottom: 0;
}
#contents .ui.basic.segment {
  margin-top: 56px;
}
#file-list {
  margin-top: 56px;
  margin-bottom: 56px;
  background: transparent;
  border: 0;
  border-radius: 0;
  box-shadow: 0 1px 0 rgba(34, 36, 38, 0.1);
}
#file-list img {
  display: inline-block;
  float: left;
  width: 18px;
  margin-right: 8px;
}
#edit {
  margin-top: 56px;
}
div .ui.modal {
  height: auto;
}
.ui.form textarea {
  resize: none;
}
